.todo{
    margin-top: 20px;
    width: 400px;
    height: 500px;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    padding: 5px;
    background-image: linear-gradient(to bottom, white, black);
    box-shadow: 0px 0px 20px black;
}
.todo-content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.title{
    padding: 15px;
    text-align: center;
}
.todo-input{
    flex: 0 0 auto;
    width: 100%;
    padding: 10px;
    max-height: 50px;
}
.todo-input input{
    width: 88%;
    height: 30px;
    border-top: 0px;
    border-right: 0px;
    border-left: 0px;
    outline: 0px;
    padding: 5px;
    background-color: rgba(240, 248, 255, 0);
}
.todo-input input:focus{
    border-bottom: 2px solid black;
}
.todo-input button{
    width: 10%;
    height: 30px;
    margin-left: 2%;
    background-color: green;
    color: white;
    border: 0px;
    outline: 0px;
    cursor: pointer;
}
.list-todo{
    min-height: calc(100% - 50px);
}
.list-todo span{
    margin-right: 5px;
}
.list-todo button{
    margin-right: 5px;
}
.item-todo{
    display: flex;
    border-bottom: 2px solid;
}
.item-todo .checkbox {
    flex: 1 0 auto;
    min-width: 50%;
    max-width: 50%;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.item-todo .checkbox label{
    margin-left: 5px;
}
.item-todo button {
    flex: 1 0 auto;
    min-width: 10%;
    padding: 5px;
    border: 0px;
    outline: 0px;
    cursor: pointer;
}
.item-todo button.button-edit{
    background-color: blue;
    color: white;
}
.item-todo button.button-delete{
    background-color: red;
    color: white;
}
.item-todo button.button-complete{
    background-color: greenyellow;
    color: white;
}
.complete{
    text-decoration: line-through;
}
.icon-complete{
    color: green;
}
.modal{
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: greenyellow;
    color: black;
    padding: 10px;
    border-radius: 15px;
}
.modal .exit{
    cursor: pointer;
    display: flex;
    flex-direction: row-reverse;
    font-size: 20px;
}
.icon {
    font-size: 20px;
    margin-right: 5px;
    color: green;
}