*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul{
  list-style: none;
  width: 100%;
}
ul li {
  padding: 10px;
  width: 100%;
}
.container{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #bebebe;
  width: 100%;
  height: 100vh;
  position: relative;
}

